import React from 'react';
import {Link} from 'react-router-dom'
import { Button, Input, Checkbox } from '@holism/core';
import axios from 'axios';
import Token from './Store'

import '../css/Reg.css';


class Auth extends React.Component {

	constructor(props){
		super(props);
		this.state = {
			valueEmail : '',
			valuePass : '',
			value1 : false	
		}

		this.handleChange = this.handleChange.bind(this);	
		this.sub = this.sub.bind(this);	
	}

	sub(event, props){
		event.preventDefault();
		axios({
		    method: 'get',
		    url: `http://gazprom.trex-studio.ru:9997/api/login?email=${this.state.valueEmail}&password=${this.state.valuePass}`
		})
			.then(function (data) {
		        //handle success
		        if(data.data.data){
			        Token(data.data.data.token);
			       	window.location.href = '/lk';
		        }
		        else{
		        	console.log(data);
		        	alert('Введен неверный логин пароль!')
		        	// console.log(`http://gazprom.trex-studio.ru:9997/api/login?${Auth.state.valueEmail}&password=${Auth.state.valuePass}`)
		        }
		    })
	}

	handleChange(event){
		console.log(event.target.value)
		if(event.target.name === 'email'){
			this.setState({valueEmail: event.target.value})
		}

		if(event.target.name === 'pass'){
			this.setState({valuePass: event.target.value})
		}

	}

	// console.log(event);
	// event.preventDefault();
	

	render(){
		return(
			<form className="main_block_reg" onSubmit={this.sub}>
				<p className="text-center block_reg__title">Войти в личный кабинет</p>
				<div className="input_reg">
					<Input placeholder="E-mail" type="email" name="email" onChange={this.handleChange} />
				</div>
				<div className="input_reg">
					<Input placeholder="Пароль" type="password" name="pass" onChange={this.handleChange} />
				</div>
				<div className="input_reg">
					<Checkbox
				        id="CheckboxID"
				        label="Запомнить меня"
				        checked={this.state.value1}
				      />
				</div>
				<Button className="button_reg" type="submit" dimension="small">Войти</Button>
				<Link to="" className="text-center link_more">
					Забыли пароль?
				</Link>
			</form>
		);

	}
}

export default Auth;